<template lang="html">
  <el-form label-position="top" label-width="80px" :model="formContent">
    <el-row :gutter="20">
      <el-col :span="formItem.col" v-for="formItem in formContent" :key="formItem.title">
      <el-form-item :label="formItem.title"
                    :class="{checkbox_form_item : formItem.type == 'checkbox'}">
        <!-- input -->
        <el-input v-if="formItem.type == 'input'"
                  v-model="formItem.value"
                  size="small">
        </el-input>
        <!-- select -->
        <el-select v-if="formItem.type == 'select'"
                   v-model="formItem.value"
                   size="small"
                   :placeholder="formItem.placeholder || ''">
          <el-option
            v-for="item in formItem.optionList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <!-- date-picker -->
        <el-date-picker v-if="formItem.type == 'datePicker'"
                        v-model="formItem.value"
                        type="daterange"
                        range-separator=""
                        start-placeholder=""
                        end-placeholder=""
                        size="small">
        </el-date-picker>
        <!-- checkbox -->
        <el-checkbox v-if="formItem.type == 'checkbox'"
                     v-model="formItem.value">
                     {{formItem.label}}
        </el-checkbox>
      </el-form-item>
    </el-col>
    <!-- slot -->
    <slot></slot>
    </el-row>
  </el-form>
</template>

<script>
import {mapState} from 'vuex'
export default {
  data() {
    return {
      formLabelAlign:{}
    }
  },
  props:{
    formContent:{
      type:Object,
      default:()=>{}
    }
  },
  computed: {

  },
  created() {
    // console.log(this.$store)
  }
}
</script>

<style lang="stylus">
.checkbox_form_item
  .el-form-item__label
    display none
  .el-form-item__content
    margin-top 28px
</style>

<style lang="stylus" scoped>
.el-date-editor--datetimerange.el-input, .el-date-editor--datetimerange.el-input__inner,.el-range-editor.el-input__inner
  width 100%

</style>



