import mainRulesData from './data/mainRulesTableData.js';
import ruleParamsData from './data/ruleParamsTableData.js';
import rewardParamsData from './data/rewardParamsTableData.js';

import FetchMock from 'fetch-mock';

// mock
FetchMock.mock('/login', (url, opts) => {
    const params = opts.params;
    if (params.account === '13917085868') {
        if (params.password === '885868') {
            return {code: 200, message: 'success'};
        }
        else {
            return {code: 401, message: ''};
        }
    }
    else {
        return {code: 400, message: ''};
    }
});

// token
FetchMock.mock('/getMobileVerifyToken', (url, opts) => {
    return {code: 200, message: 'success', mobileVerifyToken: 'abc123456'};
});
FetchMock.mock('/register/getVerifyCode', (url, opts) => {
    const params = opts.params;
    return {code: 200, message: 'success', mobile: params.mobile };
});

FetchMock.mock('/register/mobile', (url, opts) => {
    const params = opts.params;
    if (params.verifyCode === '123456') {
        return {code: 200, message: 'success'}
    }
    else {
        return {code: 400, message: 'invalid verifyCode'}
    }
});
FetchMock.mock('/register/info', {code: 200, message: 'success'});
FetchMock.mock('/save-delivery', {code: 200, message: 'success'});
FetchMock.mock('/del-delivery', {code: 200, message: 'success'});

// mainrule
FetchMock.mock('/mainRules-data', (url, opts) => {
    const params = opts.params;
    let tableData = [];
    if(params.pagination) {
      let index = params.Index
      let size = params.Size
      tableData = mainRulesData.filter((item,i) => i>=(index-1)*size && i<index*size)
    } else {
      tableData = mainRulesData
    }
    return { code: 200, message: 'success', data: {tableData : tableData,length:mainRulesData.length} }
});

// ruleparams 11111111111111111111111111
