<template lang="html">
  <el-dialog title=""
             :visible.sync="formVisible"
             width="520px"
             :close-on-press-escape="false"
             :close-on-click-modal="false"
             :before-close="handleClose">
             <el-form label-position="top" label-width="80px" :model="formData">
               <el-form-item :label="formItem.title"
                             v-for="formItem in formData"
                             :key="formItem.label"
                             :class="[{checkbox_form_item  : formItem.type == 'checkbox' || formItem.type == 'checkboxGroup'},
                                      {checkbox_form_item_modal  : formItem.type == 'checkbox' || formItem.type == 'checkboxGroup'},
                                      {checkbox_form_item_item  : formItem.type == 'checkbox'},
                                      {checkbox_form_item_group  : formItem.type == 'checkboxGroup'}]">
                  <!-- input -->
                 <el-input v-if="formItem.type == 'input'"
                           v-model="formItem.value"
                           auto-complete="off"
                           size="small">
                  </el-input>
                  <!-- select -->
                  <el-select v-if="formItem.type == 'select'"
                             v-model="formItem.value"
                             size="small"
                             :placeholder="formItem.placeholder || ''">
                    <el-option
                      v-for="item in formItem.optionList"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                  <!-- date-picker -->
                  <el-date-picker v-if="formItem.type == 'datePicker'"
                                  v-model="formItem.value"
                                  type="daterange"
                                  range-separator=""
                                  start-placeholder=""
                                  end-placeholder=""
                                  size="small">
                  </el-date-picker>
                  <!-- checkbox -->
                  <el-checkbox v-if="formItem.type == 'checkbox'"
                               v-model="formItem.value"
                               class="modal-checkbox-content">
                               {{formItem.label}}
                  </el-checkbox>
                  <!-- checkbox-group -->
                  <el-checkbox v-if="formItem.type == 'checkboxGroup'"
                               v-for="item in formItem.list"
                               :key="item.label"
                               v-model="item.value"
                               class="modal-checkbox-content">
                               {{item.label}}
                  </el-checkbox>
               </el-form-item>

             </el-form>
             <div slot="footer" class="dialog-footer">
               <el-button @click="handleClose" size="small"> </el-button>
               <el-button type="primary" @click="handleConfirm" size="small"> </el-button>
             </div>
   </el-dialog>
</template>

<script>
export default {
  props:{
    formData:{
      type:Object,
      required:true
    },
    formVisible:{
      type:Boolean,
      default:false
    }
  },
  methods:{
    handleClose() {
      this.$emit('on-close')
    },
    handleConfirm() {
      this.$emit('on-confirm')
    }
  },
  created() {
    // console.log(this.formData)
  }
}
</script>
<style lang="stylus">
  .checkbox_form_item_modal
    margin-bottom 5px
    .el-form-item__content
      margin-top 0
      line-height 1
</style>
<style lang="stylus" scoped>
.el-form-item
  margin-bottom 10px
.el-range-editor.el-input__inner
  width 100%
.modal-checkbox-content
  width 50%
  margin-bottom 5px
  margin-left 0 !important
.checkbox_form_item_group
  margin-bottom 20px
.checkbox_form_item_item
  margin-bottom 5px !important
</style>



